import React, { forwardRef } from "react";
import './Signup.css'
import { useForm } from "react-hook-form";
import NotificationManager from "react-notifications/lib/NotificationManager";
import { useDispatch, useSelector } from "react-redux";
import { useEffect, useState } from 'react';

import { useParams } from 'react-router-dom';

import {
  // MDBBtn,
  MDBContainer,
  MDBCard,
  MDBCardBody,
  MDBInput,
  MDBCheckbox,
  
} from "mdb-react-ui-kit";
import { FormControl } from "react-bootstrap";
const Signup = forwardRef((props, ref) => {
  // const dispatch=useDispatch()
  //   const {register,handleSubmit,formState:{errors}}=useForm()
  //   let users=useSelector((store)=>{
    //        return store.userSection
  //   })
  //   const saveUser = (user) => {
    //     user.id = Math.round(Math.random() * 1000)
    //     dispatch({
      //       type:'ADD_USER',
      //       payload:user
      
      //     })
      //     NotificationManager.success("User created Congrats")
      //     console.log(user)
      //   }
      
      // const {register,handleSubmit,formState:{errors}}=useForm()
  let params = useParams();
    const [user, setUser] = useState(null);
    // let [userKaIndex, setUserIndex] = useState(-1);

  let users = useSelector(function(store){
    return store.userSection
  })

  useEffect(function(){

    let userMilgya = users ? users.find(user => user.name == params.name) : null;

    if(userMilgya){
      
      let userKaIndex = users.indexOf(userMilgya);
      // setUserIndex(userKaIndex);
      setUser(userMilgya)


    }

  }, [])

  useEffect(() => {

    if(params.name){
      reset(user);
    }
    
}, [user]);

    // alert(params.name)

    let dispatch = useDispatch();

    const {register,handleSubmit,reset,formState:{errors}}=useForm()
  
    function saveUser(meraData){

      if(params.name){
        dispatch({
          // meraIndex:userKaIndex,
          type:"PERSON_EDIT",
          payload:meraData
        })

      }else{
        
        meraData.id = Math.round(Math.random() * 1000)

       dispatch({
        type:"PERSON_ADD",
        payload:meraData
      })
    }

        // props.abc.push(meraData)

        NotificationManager.success("user create hogya wa");
    //   setUsers([...users])
  
      console.log(meraData)
    }
    
  return (
    <div>

      <MDBContainer
        fluid
        className="d-flex align-items-center justify-content-center bg-image"
        style={{
          backgroundImage:
            "url(https://mdbcdn.b-cdn.net/img/Photos/new-templates/search-box/img4.webp)",
        }}
      >
        <div className="mask gradient-custom-3"></div>
        <MDBCard className="m-5" style={{ maxWidth: "600px" }}>
          <MDBCardBody className="px-5">
            <h2 className="text-uppercase text-center mb-5">
              Create an account
            </h2>
            <form onSubmit={handleSubmit(saveUser)}>

            {/* <FormGroup onSubmit={this.gotEmail} role="form" id='formh'> */}
            <FormControl type="text" className="form-control"/>

            <MDBInput {...register('name',{required:true})}
              wrapperClass="mb-4"
              placeholder="Your Name"
              size="lg"
              id="form1"
              type="text"
            />

             {errors.name? <div className="errorp">Please Enter Your Name</div>:null}
            <MDBInput {...register('email',{required:true,validate:function(typed){
        if(typed.match(
          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
            
        )){
          return true
        }else{
          return false
        }
        }})}
              wrapperClass="mb-4"
              placeholder="Your Email"
              size="lg"
              id="form2"
              type="email"
            />
             {errors.email? <div className="errorp">Please Enter Your Email</div>:null}

            <MDBInput {...register('password',{required:true,minLength:8,maxLength:12})}
              wrapperClass="mb-4"
              placeholder="Password"
              size="lg"
              id="form3"
              type="password"
            />
             {errors.password? <div className="errorp">Please Enter Your Password</div>:null}
             {errors.password && errors.password.type=='minLength'?<div className='errorp'>Password must includes at least 8 characters </div>:null}
        {errors.password && errors.password.type=='maxLength'?<div className='errorp'>Password must includes  maximum 12 characters</div>:null}
            <MDBInput
              wrapperClass="mb-4"{...register('confirmed',{required:true,validate:function(value){
                  if(document.getElementById('form3').value ==value){
                    return true
                  }
                  else{
                    return false
                    }
                  }})}
              placeholder="Repeat your password"
              size="lg"
              id="form4"
              type="password"
            />
             {errors.confirmed && errors.confirmed.type == "required" ? <div className='errorp'>Please Enter The password confirmed</div>:null}
        {errors.confirmed && errors.confirmed.type=='validate'?<div className='errorp'>Confirmed password should matches the previous password</div>:null}
            <div className="d-flex flex-row justify-content-center mb-4">
              <MDBCheckbox {...register('check',{required:true})}
                name="flexCheck"
                id="flexCheckDefault"
                label="I agree all statements in Terms of service"
              />
             {/* {errors.check? <div className="errorp">Please accept all terms and conditions</div>:null} */}

            </div>
            <button className="mb-4 w-100 gradient-custom-4" size="lg" type="submit">
              Register
            </button>
            <FormControl/>
{/* // </FormGroup> */}
            </form>
          </MDBCardBody>
        </MDBCard>
      </MDBContainer>
    </div>
  );
})
export default Signup;



