import {createStore} from 'redux'
let initialData={
  users:[]
}
function userSection(oldData=initialData,newData){
if(newData.type==='ADD_USER'){
  oldData.push(newData.payload)
}
else if(newData.type == "PERSON_DELETED"){
  oldData.splice(newData.meraIndex, 1);
}else if(newData.type == "PERSON_EDIT"){

  let someIndex = oldData.findIndex(user=>user.id == newData.payload.id);
  oldData[someIndex]  = newData.payload;
}

  return [...oldData]
}


export let meraStore=createStore(userSection)