import { useDispatch, useSelector } from "react-redux"
import {Link} from 'react-router-dom'
export default (props)=>{

    let dispatch = useDispatch();

    let users = useSelector(function(store){
        return store.userSection;
    })

 return <div>
    <table>
        {
            users.map(function(user, index){
                return <tr>
                    <td>{user.name}</td>
                    <td>{user.password}</td>
                    <td>
                        <button onClick={()=>{

                            dispatch({
                                type:"PERSON_DELETED",
                                meraIndex:index

                            });

                        }}>Delete</button>
                    </td>
                    <td>
                        <Link to={"/signup/"+user.name}>Edit</Link>
                    </td>
                </tr>
            })
        }
    </table>
 </div>

}