import React from 'react'
import './Login.css'
import { useForm } from "react-hook-form";
import NotificationManager from "react-notifications/lib/NotificationManager";

import {
  MDBBtn,
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput
}
from 'mdb-react-ui-kit';
export default function Login(props) {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
 
  return (
    <div>

    <MDBContainer className="my-5 gradient-form">

      <MDBRow>

        <MDBCol col='6' className="mb-5">
          <div className="d-flex flex-column ms-5">

            <div className="text-center">
              <img src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-login-form/lotus.webp"
                style={{width: '185px'}} alt="logo" />
              <h4 className="mt-1 mb-5 pb-1">We are The Developers</h4>
            </div>
<strong className='container text-center my-3'>

            <p>Please login to your account</p>
</strong>

<form  onSubmit={handleSubmit(function (myValue) {
                let userFound = props.users.find(function(use) {
                  if (
                    use.email === myValue.email &&
                    use.password === myValue.password
                  ){
                    return true;
                  }
                });
                if (userFound) {
                  NotificationManager.success("Congrats,User Found");
                } else {
                  NotificationManager.error(
                    " User could not found sorry "
                  );
}
})}>

            <MDBInput 
                 {...register('email',{required:true})}
             wrapperClass='mb-4 my-3' placeholder='Entere your email' id='form1' type='email'/>
                {errors.email? <div> Please Enter your Email</div>:null}

            <MDBInput wrapperClass='mb-4 my-2' placeholder='Entere your password'  id='form2' type='password'/>


            <div className="text-center pt-1 mb-5 pb-1">
              <MDBBtn className="mb-4 w-100 gradient-custom-2">Sign in</MDBBtn>
              <a className="text-muted" href="#!">Forgot password?</a>
            </div>

</form>
            <div className="d-flex flex-row align-items-center justify-content-center pb-4 mb-4">
              <p className="mb-0">Don't have an account?</p>
              <MDBBtn outline className='mx-2' color='danger'>
                Create new
              </MDBBtn>
            </div>

          </div>

        </MDBCol>

        <MDBCol col='6' className="mb-5">
          <div className="d-flex flex-column  justify-content-center gradient-custom-2 h-100 mb-4">

            <div className="text-white px-3 py-4 p-md-5 mx-md-4">
              <h4 class="mb-4">We are more than just a company</h4>
              <p class="small mb-0">Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod
                tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud
                exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.
              </p>
            </div>

          </div>

        </MDBCol>

      </MDBRow>

    </MDBContainer>
  

    </div>
  )
}
